import { getInfo, setInfo } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      // 个人权证相关
    //   userInfo: {
    //     token: '',
    //     userId: ''
    //   }
      userInfo: getInfo()
    }
  },
  mutations: {
    // 所有mutations的第一个参数都是state，第二个才是形参
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      // 个人信息要重置
      context.commit('setUserInfo', {})

      // 购物车信息要重置(跨模块调用 mutation) cart/setCartList root:true表示全局
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {}
}
